.game-window {
    box-sizing: unset;
    position: relative;

    width: 500px;
    height: 300px;

    margin: 30px auto 30px;
    border: 1px solid black;
    
    overflow-x: hidden;
    overflow-y: hidden;

    background-image: url('./images/bg.png');
    background-position: 0 0;
}